import "react-accessible-accordion/dist/fancy-example.css";
import NavPills from "../Common/NavPills";
import { Routes, Route, Navigate } from "react-router-dom";
import { ShipDesc } from "components/Common/ImageTextBuilder";

export default function DayOneTips() {
  return (
    <>
      <h1 className="text-center">Day One Tips</h1>
      <p className="text-center">
        Welcome to Azur Lane. <br />
        This section of the guide will help you start the game and address common misconceptions you will encounter early on.
      </p>
      {/* These are the nav pills at the top. 
          You need to specify the base route with a trailing slash. 
          Then you have the individual sub routes with a name for the nav pill and a path for the route.
          The path is what you need to use for links.
          E.g. to link to the Limit Breaks section, you would use `href="#/newbietips/dayonetips/limitbreaks"`.
      */}
      <NavPills base="newbietips/dayonetips/" routes={[
        { name: "Guild Shop", path: "guildshop" },
        { name: "Starter Fleet", path: "starterfleet" },
        { name: "Limit Breaks", path: "limitbreaks" },
        { name: "When To Pull On Gacha", path: "gachapull" },
        { name: "Perma URs are a TRAP", path: "ursareatrap" },
        { name: "Out Of Ammo", path: "outofammo" },
        { name: "Rerolling", path: "rerolling" },
        { name: "Avoid Requisition", path: "avoidrequisition" },
        { name: "Avoid Youtube Guides", path: "avoidytguides" },
      ]}></NavPills>

      {/* This is the main content of the page.
          It replaces the old `TabContent` and `TabPane` components.
      */}
      <Routes>
        {/* This is the default route, it will redirect to the first sub route. */}
        <Route path="/" element={
          <Navigate to="guildshop" replace={true}></Navigate>
        }></Route>
        {/* Guild Shop */}
        <Route path="guildshop" element={
          <>
            <p>
              Every newbie starts with 4000 guild coins and guild coins are easy to come by if you are in an active guild. <br />
              To find the guild shop, click Shop {'>'} Supply {'>'} Supply {'>'} Guild. <br />
              The following are ships that are <b>HIGHLY RECOMMENDED</b> to start with and items you should grab if they are available: <br />
            </p>
            <div className="text-center">
              <img
                alt="..."
                src={require("../../assets/img/others/newbie_tips/guild_shop.png")}
                style={{ width: "90%", height: "90%" }}
              />
            </div>
            <br />
            <p>
              First, buy Unicorn (elite) and Portland (rare) from the shop and everything else of interest. <br />
              Second, Refresh the shop (top right), this will cost 50 guild coins but guild coins are an abundance if you participate in guild activities.
              Finally, buy Helena (elite) and Pennsylvania (rare) and everything else of interest.
            </p>
            <br />
            <p>
              These ships are all very resource efficient and some of them are required to progress your newcomer missions. <br />
              Unicorn is a very strong mob fleet healer that you will use throughout the game. <br />
              You can choose to skip Pennsylvania if you already have a good battleship that you would like to use, preferably not a UR because they will take a while to limit break.
            </p>
            <p>
              You can find more details about the <a href="#/newbietips/shops/guildshop" rel="noopener noreferrer">Guild Shop in the Shops section.</a>
            </p>
          </>
        }></Route>
        {/* Starter Fleet */}
        <Route path="starterfleet" element={
          <>
            <p>
              Your choice of starter does not matter as each of them perform about the same and they all can drop from every campaign stage.
            </p>
            <div className="text-center">
              <img
                alt="..."
                src={require("../../assets/img/others/newbie_tips/starter_fleet.png")}
                style={{ width: "90%", height: "90%", padding: "10px" }}
              />
            </div>
            <p>
              Without any use of gacha this is the recommended starter fleet, assuming you have purchased the recommended ships from the guild shop. <br />
              Javelin (front right slot) is to be replaced with who you chose to be your starter. <br />
              The empty slot can be any other ship you have available, preferably a decent one. <br />
              If you have pulled on the gacha or have recieved a ship you want to use, it is recommended to replace Pennsylvania with a decent Battleship or fill the extra slot. <br />
              The other four ships will be very important for progression. <br />
            </p>
            <ShipDesc name="Unicorn"
              rarity="elite"
              roles="Healer"
              description="An exceptional healer for the mob fleet after retrofitting. She will be used throughout the entire game."
            />
            <ShipDesc name="Portland"
              rarity="rare"
              roles="Tank"
              description="Required for newcomer missions and is a decent tank that is good enough to progress into late game."
            />
            <ShipDesc name="Helena"
              rarity="elite"
              roles="Support"
              description="A support ship with one of the strongest debuffs in the game, although a little inconsistent. She will be someone you build boss fleets around to utilize her debuff."
            />
            <ShipDesc name="Laffey,Z23,Javelin"
              rarity="elite,elite,elite"
              roles="Damage"
              description="A starter is required for newcomer missions. They are all decent ships that are able to pull their weight through the mid game (around chapter 12)."
            />
            <br />
            <p>With this fleet setup, you can easily push to chapter 9, with some gear progession along the way.
              Once you reach chapter 9, you will want to start building your second fleet by utilizing oil capped stages for a more efficient experience grind. </p>
          </>
        }></Route>
        {/* Limit Breaks */}
        <Route path="limitbreaks" element={
          <>
            <div className="text-center">
              <img
                alt="..."
                src={require("../../assets/img/others/newbie_tips/limit_break.png")}
                style={{ width: "100%", height: "100%" }}
              />
            </div>
            <br />
            <p>
              Max Limit break (MLB) your ships as soon as possible. <br />
              Limit breaks are massive power spikes for every ship, making progression significantly easier. While UR ships may have powerful skills, they will perform worse than most other ships if they are not fully limit broken.
              Limit breaks provide a ton of stats and most notably extra guns and/or planes. Extra gun bases and aircrafts give your ships extra shots/planes per reload, providing a massive damage multiplier. <br />
              Limit breaking ships causes them to cost more oil, but trying to save a small amount of oil at the expense of smoother progression is not worth it.
            </p>
            <p>
              Gold Bulins are somewhat scarce, primarily obtained from weekly missions, the monthly medal shop and PvP, so use them wisely. <br />
              Avoid spreading Bulins across multiple ships whenever possible, especially on ships that are not in use. <br />
              It is better to fully MLB one ship than to have several partially limit-broken ships. This is especially true for UR ships, as UR Bulins are heavily time-gated.
            </p></>
        }></Route>
        {/* When To Pull On Gacha */}
        <Route path="gachapull" element={
          <>
            <div className="text-center">
              <img
                alt="..."
                src={require("../../assets/img/others/newbie_tips/builds.png")}
                style={{ width: "100%", height: "100%" }}
              />
            </div>
            <br />
            <p>
              Cubes should only be used on event banners, indicated by the text that says <b>EVENT</b>. <br />
              The event pool will use one of the other permanent pools as base with event ships added at their indicated rates. <br />
              Generally, players will only pull during events for one copy of each ship and save in between events. The game is generous enough where the expectation is to pull 1 copy of each ship once you have some cubes stockpiled and RNG doesn't fuck you in the ass.
            </p>
            <p>
              You should be doing your daily pull on the light pool since it costs one cube and the daily mission rewards one cube.
              It is not recommended to do daily pulls on other pools, but it's your cubes.
            </p></>
        }></Route>
        {/* // Perma URs are a TRAP */}
        <Route path="ursareatrap" element={
          <><p>
            <b>Do not pull for permanent URs!! </b> <br />
            You will get them indirectly from events. If you pull on the permanent pools, you will wastefully deplete your cubes and you will not have enough cubes to pull on future events.
          </p>
            <p>
              The permanent UR pity allows you to obtain a UR of choice after 200 pulls and these pulls include NON-UR EVENT banners. <br />
              Ideally you would want to only pull during events and you will get the permanent URs for free.
              Even if you are able to get a UR without hitting pity, she will very likely not be usable for at least two months because Bulins for them are timegated and non-MLB ships are worse than every MLB ship in the game.
            </p></>
        }></Route>
        {/* My Fleet Is Out Of Ammo!!! */}
        <Route path="outofammo" element={
          <>
            <div className="text-center">
              <img
                alt="..."
                src={require("../../assets/img/others/newbie_tips/ammo.png")}
                style={{ width: "50%", height: "50%" }}
              />
            </div>
            <br />
            <p>
              A common misconception among new players is assuming you can not fight on zero ammo. <br />
              You can continue fighting when at zero ammo. You only suffer a -50% damage penalty. <br />
              You can check the effects of a buff by clicking on it on the top left while in the map. Most notably Adequate Ammo (4+ ammo) provides +10% damage and Out of Ammo (0 ammo) gives -50% damage.
            </p>
            <p>
              The most common strategy in the later stages is to have one fleet to kill all the mobs (Mob Fleet) and a second fresh fleet to kill the boss (Boss Fleet).
              The Mob fleet is built to be able to sustain and kill all the mobs, even when suffering from zero ammo. <br />
            </p></>
        }></Route>
        {/* // Rerolling */}
        <Route path="rerolling" element={
          <><p>
            It is not recommended to reroll accounts due to a majority of cubes being timegated rewards and the game does not frontload you with many cubes. <br />
            In the permanent pool, there are not very many ships that will help you early on and you want to use the starter fleet because many of the newcomer missions require specific ships to be used.
          </p>
            <p>
              If you still want to reroll an account, the best opportunity is during an UR event, where the UR ship is the trophy pull. <br />
              Doing so is a very tedious chore, as you do not get very many cubes in the beginning. <br />
              Even if you pull a UR, she will not be usable for months since UR Bulins are a monthly timegated resource.
            </p></>
        }></Route>
        {/* // Avoid Requisition */}
        <Route path="avoidrequisition" element={
          <><p>
            Requisition is a massive currency trap, especially for new players. <br />
            This is a gacha that costs medals instead of cubes with rates worse than the normal gacha. All SR ships in this pool are available in the medal shop for 80 medals, which is lower than the expected rate of hitting one. <br />
            It is better to buy the ships you want from the shop instead of testing your rng for it and there are also plenty of useful items in the medal shop as well.

          </p></>
        }></Route>
        {/* // Avoid Youtube Guides */}
        <Route path="avoidytguides" element={
          <><p>
            A majority of Youtube guides are often outdated, misinformed and/or clickbait. (Most often all of the above) <br />
            Your best source of information will be from AL Discord servers and/or from people who actually play the game. <br />
            You can find the link to our discord in the navigation bar at the top.
          </p></>
        }></Route>
      </Routes>
    </>
  );
};