import {
  Card,
  CardBody,
  Container,
  Col,
  CardHeader,
} from "reactstrap";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import DayoneTips from "../components/NewbieTips/DayOneTips.js";
import Progression from "../components/NewbieTips/Progression.js";
import FleetsAndGear from "../components/NewbieTips/FleetsAndGear.js";
import Research from "../components/NewbieTips/Research.js";
import Shops from "../components/NewbieTips/Shops.js";
import OtherTips from "../components/NewbieTips/OtherTips.js";
import SubPageBackground from '../components/Common/SubPageBackground';
import ScrollToTopButton from '../components/Common/ScrollToTopButton';
import { useEffect } from 'react';
import { Navigate, Route, Routes } from "react-router-dom";
import NavPills from "../components/Common/NavPills";
  
export default function NewbieTips() {
  useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;

    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <SubPageBackground>
        <ScrollToTopButton></ScrollToTopButton>

        <IndexNavbar isStatic={true} />
        <Container style={{ color: "#000" }}>
          <Col>
            <Card className="text-center ectl-primary">
              <CardHeader>
                <br />
                <h2>Newbie Tips</h2>
                <p>
                  Written by: Riceist <br />
                  Updated: June 3, 2025
                </p>
                <NavPills base="newbietips/" routes={[
                  { name: "Day One Tips", path: "dayonetips" },
                  { name: "Progression", path: "progression" },
                  { name: "Fleets and Gear", path: "fleetsandgear" },
                  { name: "Research", path: "research" },
                  { name: "Shops", path: "shops" },
                  { name: "Other Helpful Tips", path: "othertips" },
                ]}
                ></NavPills>
              </CardHeader>
              <CardBody className="text-left">
                <hr />
                {/* Since these routes have sub-routes, they need the `/*` at the end. */}
                <Routes>
                  <Route path="/" element={
                    <Navigate to="dayonetips" replace={true} />
                  }></Route>
                  <Route path="dayonetips/*" element={
                    <DayoneTips />
                  }></Route>
                  <Route path="progression/*" element={
                    <Progression />
                  }></Route>
                  <Route path="fleetsandgear/*" element={
                    <FleetsAndGear />
                  }></Route>
                  <Route path="research/*" element={
                    <Research />
                  }></Route>
                  <Route path="shops/*" element={
                    <Shops />
                  }></Route>
                  <Route path="othertips/*" element={
                    <OtherTips />
                  }></Route>
                </Routes>
              </CardBody>
            </Card>
          </Col>
        </Container>
      </SubPageBackground>
      <DarkFooter />
    </>
  );
};