import { useLocation } from "react-router-dom";
import { Nav, NavItem, NavLink } from "reactstrap";
import { useEffect, useState } from "react";

/**
 * Remove the hash and possible leading slash.
 * 
 * @param {string} path 
 * @returns {string}
 */
function cleanPath(path) {
  let clean = path.startsWith("#") ? path.substring(1) : path;
  clean = clean.startsWith("/") ? clean.substring(1) : clean;

  return clean;
}

/**
 * 
 * @param {{ base: string, routes: {name: string, path: string}[] }} props
 */
export default function Image(props) {
  const [iconPills, setIconPills] = useState(0);
  const location = useLocation();

  const cleanedBase = cleanPath(props.base);

  useEffect(() => {
    const cleanedLocation = cleanPath(location.pathname);

    const index = props.routes.findIndex(route => cleanedLocation.startsWith(cleanedBase + route.path));
    setIconPills(index === -1 ? 0 : index);
  }, [location, cleanedBase, props.routes]);

  return (
    <Nav
      className="justify-content-center"
      role="tablist"
      tabs
    >
      {props.routes.map((route, index) => (
        <NavItem>
          <NavLink
            className={(iconPills === index ? "active" : "") + " ectl-nav-pill"}
            href={`#/${cleanedBase}${route.path}`}
          >
            {route.name}
          </NavLink>
        </NavItem>
      ))}
    </Nav>
  )
}