
/**
 * 
 * @param {name:string, rarity:string, roles:string, description:string}
 * @returns 
 */

export function ShipDesc({ name, rarity, roles, description }) {
  const namelist = name.split(",");
  const raritylist = rarity.split(",");
  let ships = [];
  let images = [];
  for (let i = 0; i < namelist.length; i++) {
    var shipname = namelist[i].trim();
    var shipurl = shipname.replace(/\s/g, "_");
    if (i > 0)
      ships.push(" / ");

    ships.push(
      <a
        target="_blank"
        rel="noopener noreferrer"
        href={`https://azurlane.koumakan.jp/wiki/${shipurl}`}
      > {shipname} </a>);

    images.push(
      <div className="flex-shipicons-float">
        <a
          target="_blank"
          rel="noopener noreferrer"
          href={`https://azurlane.koumakan.jp/wiki/${shipurl}`}
        >
          <img
            alt="..."
            src={require(`../../assets/img/ship_images/${shipname.toLowerCase()}icon.png`)}
            className={`img-raised rarity-${raritylist[i]}`}
            style={{ width: "80px", height: "80px", padding: "5px", borderradius: "5px" }}
          />
        </a>
      </div>)
  }

  return (
    <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between"}}>
      <div className="image-flexwrap">
        {images}
      </div>
      <div className="flexgrow-centered-text">
        <p><b>{ships}</b> <br />
          <b>Role: {roles}</b> <br />
          {description}
        </p>
      </div>
    </div>
  )
}

